export const preprocessPaths = (paths = {}) => {
  return paths.map((row) => ({
    type: "scattermapbox",
    lat: row.lng,
    lon: row.lat,
    mode: "lines",
    line: { width: 4, color: "red" },
    id: row.id,
    text: `Connection between ${row.id}`,
  }));
};

