export const fillNodesMetricData = (metrics = {}, metricKey = 'tw', usePrev = false, prevMetrics, prevMetricTwo) => {
  return (node = {}) => {
    const x = Object.keys(metrics)
    const y = Object.values(metrics).map(hourData => {
      const currMetric = hourData.find(metr => metr.p === node.name);
      if(currMetric == null) {
        return 0;
      }
      return currMetric[metricKey];
    })
    return {
      ids: node.name,
      mode: 'lines+markers',
      connectgaps: true,
      name: node.label,
      line: {
        color: node.color,
        dash: node.lineType,
        width: 4
      },
      x: x,
      y: y,
    };
  };
};
